.image-category {
    border: 3px solid rgb(138, 126, 126);
    border-radius: 10px;
    display: flex; align-items: center; justify-content: center;
    transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    box-shadow: rgb(0, 0, 0) 0px 25px 20px -20px;
}

.image-category:hover {
    transform: scale(1.05);
    border: 3px solid rgb(231, 228, 228);
}

.image-category .image-png{
    height: 105%; width: 105%;
}

.video-tag {
    width: 110%; height: 110%;
    position: absolute;
    display: none;
}

.image-category:hover .video-tag {
    display: flex;
}

.image-category:hover .image-png {
    display: none;
}
