.form-log-in-sign-up {
    width: 370px; height: 60%;
    display: flex; flex-flow: column;
    align-items: center; justify-content: space-between;
}

.form-log-in-sign-up .input-label-login{
    display: flex; flex-flow: column;
    align-items: flex-start; justify-content: space-between;
    width: 100%; height: 95px;
}

.form-log-in-sign-up .input-label-login input {
    width: 100%; height: 40px;
    outline: none;
    border: 1px solid grey;
    background-color: rgba(66, 66, 66, 0.505);
    border-radius: 6px;
    padding: 23px 15px;
    color: white; 
    font-size: 16px;
}

.form-log-in-sign-up label {
    color: white;
    font-size: 24px;
    font-weight: 600;
}


.button-login-section {
    width: 100%; height: 48px;
}

.button-login-section .button-login {
    width: 100%; height: 100%;
    border: none;
    border-radius: 6px;
    background-color: rgb(17, 137, 229);
    color: white;
    font-size: 18px;
}

.sign-up-section {
    width: 370px;
    margin-top: 30px;
    display: flex; flex-flow: row;
    gap: 10px;
    color: rgba(255, 255, 255, 0.768);
}

.form-data-carousel {
    width: 100%; height: 100%;
    display: flex; 
    position: relative;
    overflow: hidden;
    /*background-color: rgb(16, 43, 90);*/
    gap: 10px;

}

.form-data-carousel .email-section {
    min-width: 370px; height: 100%;
    max-width: 370px;
    display: flex; flex-flow: column;
    justify-content: space-evenly;
    transition: all 350ms ease-in-out;
}

.pdw-section {
    position: relative;
    min-width: 370px; height: 100%;
    display: flex; flex-flow: column;
    justify-content: space-evenly;
    transition: 350ms ease-in-out;
    opacity: 1;
}
